<template>
  <div>
    <div class="search_container">
      <el-form :inline="true" :model="params" class="demo-form-inline">
        <el-form-item label="销售组织" size="mini">
          <el-select clearable v-model="params.company_id" @focus="companyListInvok" @change="clearBusiss" placeholder="请选择" filterable>
            <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
              <span :class="item.type==1 ? 'optionStyle': '' ">{{ item.company_name }}</span>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="客户分类" size="mini">
          <el-select clearable v-model="params.customer_type_id" @focus="customerTypeInvok" @change="clearCustomer">
            <el-option :label="item.customer_type_name" :value="item.customer_type_id" :key="item.customer_type_id" v-for="item in customerTypeList"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="客户名称" size="mini">
          <el-select v-model="params.customer_id" filterable placeholder="请输入" @focus="clienteleListInvok" @change="getCustomerChange" :loading="loading" remote :remote-method="clienteleListInvok">
            <ul class="customer customer-ul4 customer-ul2">
              <li>客户编号</li>
              <li>客户名称</li>
              <li>联系人</li>
              <li>联系电话</li>
              <li>区域</li>
            </ul>
            <el-option v-for="item in customerList" :key="item.customer_id" :label="item.customer_name" :value="item.customer_id">
              <ul class="customer customer-ul4">
                <li :title="item.customer_id">{{ item.customer_id }}</li>
                <li :title="item.customer_name">{{ item.customer_name }}</li>
                <li :title="item.contact_name">{{item.contact_name}}</li>
                <li :title="item.customer_mobile">{{item.customer_mobile}}</li>
                <li>{{item.province_name}}/{{item.city_name}}/{{item.area_name}}</li>
              </ul>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item size="mini" class="search-style search">
          <el-button @click="onSearch" size="mini" type="primary">查询</el-button>
          <el-button plain @click="onReset" size="mini">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="table_container">
      <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.tableData" max-height="600" style="width: 100%" @selection-change="handleSelectionChange">
        <el-table-column label="操作" fixed="right" width="40">
          <template slot-scope="scope">
            <el-button @click="examine(scope.row)" type="text" size="small" v-if="authority.b_info==1">
              查看
            </el-button>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="company_name" label="销售组织"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="customer_name" label="客户名称"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="customer_type_name" label="客户分类"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="credit_balance" align="right" label="本年固定授信总额度">
          <template slot-scope="scope">
            {{scope.row.credit_balance|priceFormat}}
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="temporary_credit_balance" align="right" label="本年临时授信总额度">
          <template slot-scope="scope">
            {{scope.row.temporary_credit_balance|priceFormatNone}}
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="use_credit" align="right" label="本年累积使用授信额度">
          <template slot-scope="scope">
            {{scope.row.use_credit|priceFormat}}
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="credit_occupying_price" align="right" label="本年累积授信占用额度">
          <template slot-scope="scope">
            {{scope.row.credit_occupying_price|priceFormat}}
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="surplus_credit" align="right" label="当前可使用授信额度">
          <template slot-scope="scope">
            {{scope.row.surplus_credit|priceFormatNone}}
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="return_credit" align="right" label="本年已回款授信总额度">
          <template slot-scope="scope">
            {{scope.row.return_credit|priceFormat}}
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="non_return_credit" align="right" label="本年未回款授信总额度">
          <template slot-scope="scope">
            {{scope.row.non_return_credit|priceFormat}}
          </template>
        </el-table-column>
        <!-- 2018-11-2 大伟说让先去掉 以及贺晓飞说的是否停用展示项去掉 <el-table-column :show-overflow-tooltip="true" property="is_disabled" label="是否停用">
          <template slot-scope="scope">
            <span v-if="scope.row.is_disabled==1">是</span>
            <span v-if="scope.row.is_disabled==0">否</span>
          </template>
        </el-table-column> -->
      </el-table>
    </div>
    <div class="page_container">
      <div class="pageNumber">每页显示</div>
      <el-pagination class="erp-pagination" @size-change="handleSizeChange" :current-page.sync="goPage" :page-size="pageSize1" @current-change="handleCurrentChange" :page-sizes="per_page" layout="total,sizes, prev, pager, next,jumper" prevText="上一页" nextText="下一页" :total="TableData.total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
import { getData, opthionList } from "common/utils/public_function/commonUtils";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "customeCreditfindDefault",
  data() {
    return {
      authority: {},
      timeValue: [],
      companyList: [],
      TableData: {
        tableData: [],
        per_page: [30, 50, 100, 500],
        total: 10
      },
      per_page: [30, 50, 100, 500],
      pageSize: 30,
      multipleSelection: [],
      is_disableds: false,
      goPage: 1, //默认搜索页面
      loading: false,
      isSearch: true //检测第几次点击搜索
    };
  },
  computed: {
    ...mapGetters(["creditfindDefault_showGet"])
  },
  methods: {
    ...mapActions(["creditfindSearch_show", "creditfindIndexACT"]),
    clearBusiss(val) {
      // this.$set(this.searchParams, "business_id", null);
      if (val) {
        this.params.company_name = this.companyList.filter(
          obj => obj.company_id === val
        )[0].company_name;
      }
    },
    getCustomerChange(val) {
      if (val) {
        this.params.customer_name = this.customerList.filter(
          obj => obj.customer_id === val
        )[0].customer_name;
      }
    },
    clearCustomer(val) {
      this.$set(this.params, "customer_id", "");
      if (val) {
        this.params.customer_type_name = this.customerTypeList.filter(
          obj => obj.customer_type_id === val
        )[0].customer_type_name;
      }
    },
    changeDate(val) {
      this.params.start_time = val[0];
      this.params.end_time = val[1];
    },
    onSearch() {
      if (this.timeValue && this.timeValue.length == 2) {
        this.params.start_time = this.timeValue[0];
        this.params.end_time = this.timeValue[1];
      } else {
        this.params.start_time = this.params.end_time = "";
      }
      this.is_disableds == true
        ? (this.params.is_disabled = 1)
        : (this.params.is_disabled = "");

      Object.assign(this.searchParams.params, this.params, {
        page: 1
      });
      this.creditfindIndexACT({
        params: this.params,
        goPage: this.goPage,
        pageSize: this.pageSize
      });
      getData(this.searchParams);
    },
    onReset() {
      this.timeValue = [];
      this.is_disableds = false;
      this.$set(this, "params", {});
      this.$set(this, "searchParams", {
        params: {},
        TableData: this.TableData, //列表数据
        API: "getCustomerCreditList", //接口名
        vm: this //传递的this
      });
      this.onSearch();
    },
    examine(row) {
      this.creditfindSearch_show({
        // commont: "creditfindSearch",
        customer_id: row.customer_id,
        company_id: row.company_id,
        customer_name: row.customer_name,
        company_name: row.company_name,
        customer_type_name: row.customer_type_name,
        params: this.params,
        goPage: this.goPage,
        pageSize: this.pageSize
      });
      this.$router.push({
        path: "/custom/creditfind/creditfind_search"
      });
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    handleSizeChange(val) {
      Object.assign(this.searchParams.params, {
        per_page: val,
        page: 1
      });
      getData(this.searchParams);
    },
    handleCurrentChange(val) {
      Object.assign(this.searchParams.params, {
        page: val
      });
      getData(this.searchParams);
    },
    getCustomerType() {
      this.$common.options("getCustomerTypeListOption", this.customerType, {
        company_id: this.params.company_id
      });
    }
  },
  mounted() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    if (
      this.creditfindDefault_showGet &&
      this.creditfindDefault_showGet.params
    ) {
      this.$set(this, "params", this.creditfindDefault_showGet.params);
      // bindIdsF(this, this.creditfindDefault_showGet.params, [
      //   ["company_id", "company_name", "companyList"],
      //   ["customer_id", "customer_name", "customerList", "options"],
      //   ["customer_type_id", "customer_type_name", "customerType", "options"]
      // ]);
      opthionList(this, this.creditfindDefault_showGet.params);
    }
    if (
      this.creditfindDefault_showGet &&
      this.creditfindDefault_showGet.goPage
    ) {
      this.params.page = this.creditfindDefault_showGet.goPage;
      this.params.per_page = this.creditfindDefault_showGet.pageSize;
    }
    this.$set(this, "searchParams", {
      params: this.params, //1默认带的参数
      TableData: this.TableData, //列表数据
      API: "getCustomerCreditList", //接口名
      vm: this //传递的this
    });
    getData(this.searchParams);
  }
};
</script>

